
import { GameList } from "../src/list/GameList";
import { PageList } from "../src/list/PageList";
import { Assets } from "../src/list/Assets";
import { useRouter } from "next/router";
import { useEffect, useState } from "react";
import dynamic from 'next/dynamic';
import { Link } from "react-router-dom";
import SkeletonLoader from '../src/components/SkeletonLoader';

const Headcom = dynamic(() => import('../src/components/Headcom'), {  });
const CatBtn = dynamic(() => import('../src/components/CatBtn'), {  });
const HomeDis = dynamic(() => import('../src/Description/HomeDis'), {  });
const FooterDis = dynamic(() => import('../src/Description/FooterDis'), {  });
const CookieCom = dynamic(() => import('../src/components/CookieCom'), {  });
const FooterCom = dynamic(() => import('../src/components/FooterCom'), {  });
const CategoryGames = dynamic(() => import('../src/components/CategoryGames.js'), {  });
const Game_Components = dynamic(() => import('../src/components/Game_Components'), {  });
const BlackScreen = dynamic(() => import('../src/components/BlackScreen'), {  });
const Snow_fx = dynamic(() => import('../src/fx/Snow_fx'), {  });
const Ad_320x50 = dynamic(() => import('../src/ads/Ad_320x50'), {  });
const Ad_728x90_Bot = dynamic(() => import('../src/ads/Ad_728x90_Bot'), {  });


const HorizontalScroll = dynamic(() => import('../src/components/HorizontalScroll'), {
  loading: () => <SkeletonLoader/>,
});
export default function HomePage() {

  const path = useRouter();
  const [mobile, setMobile] = useState(false);

  
  useEffect(() => {
    if (typeof navigator !== "undefined") {
      const checkMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
        navigator.userAgent
      );
      setMobile(checkMobile);
    }
  }, []);

// *************************************************************

const first50Games = GameList.slice(0, 100);


const getGamesByTag = (tag) => {
  try {
    if (!GameList || !Array.isArray(GameList)) {
      console.error('GameList is undefined or not an array');
      return [];
    }

    const filteredGames = GameList.filter(game => game.tag.includes(tag));

    // while (filteredGames.length < 10) {
    //   filteredGames.push(...filteredGames.slice(0, 10 - filteredGames.length));
    // }

    return filteredGames.slice(0, 30);
    // return filteredGames;
  } catch (error) {
    console.error('Error filtering games:', error);
    return [];
  }
};



  return (
    <>
      <div className="app_cont_game">
        <Headcom
          title={PageList[0].title}
          s_dis={PageList[0].s_dis}
          path={PageList[0].path[1] + PageList[0].path[0]}
          thumb={PageList[0].thumb}
          logo_16={Assets[0].logo_16}
          // logo_32={Assets[0].logo_32}
          logo_192={Assets[0].logo_192}
          logo_270={Assets[0].logo_270}
          logo_512={Assets[0].logo_512}
        />
        <div>
        
          <BlackScreen />
          
          <div className="game-root">
            <div className="container">
                 <div className="more_games" style={{paddingLeft: `var(--Left-Padding)`}}> 
          </div>
          
            {/* <Game_Components/> */}
            
            <HorizontalScroll games={getGamesByTag('Trending')} viewMoreUrl={"/categories/trending-unblocked-games"} category="Trending Games" layoutType={"single"}/> 
            <HorizontalScroll games={first50Games} viewMoreUrl={"/categories/new-games"} category="New Games" layoutType={"double"}/> 
            <HorizontalScroll games={getGamesByTag('Shooting')} viewMoreUrl={"/categories/shooting-games"} category="Shooting Games" layoutType={"single"}/> 
            <HorizontalScroll games={getGamesByTag('Flash')} viewMoreUrl={"/categories/flash-games"} category="Flash Games" layoutType={"single"}/> 
            <HorizontalScroll games={getGamesByTag('GBA')} viewMoreUrl={"/categories/gba-games"} category="Gameboy Advance Games" layoutType={"single"}/> 
            
              {mobile && <Ad_320x50 />} 
              {!mobile && <Ad_728x90_Bot />} 
              <div className="more_games" style={{paddingLeft: `var(--Left-Padding)`}}>
            <h2 style={{paddingLeft: `10px`,fontSize: `22px`, color: `#ffffff`}}>All Games</h2>
          </div>
            <CategoryGames games={first50Games}/>
            {mobile && <Ad_320x50 />} 
              {!mobile && <Ad_728x90_Bot />} 
           <HorizontalScroll games={getGamesByTag('Papas')} category="Papa's Games" layoutType={"single"}/> 
           <HorizontalScroll games={getGamesByTag('Subway Surfers')} viewMoreUrl={"/categories/subway-surfers"} category="Subway Games" layoutType={"single"}/> 
    <HorizontalScroll games={getGamesByTag('Ragdoll')} category="Ragdoll Games" layoutType={"single"}/> 
    <HorizontalScroll games={getGamesByTag('FNAF')} category="FNAF Games" layoutType={"single"}/> 
    <HorizontalScroll games={getGamesByTag('Duck')} category="Duck LIfe Games" layoutType={"single"}/> 


    <div className="more_games" style={{paddingLeft: `var(--Left-Padding)`}}>
        <h2 style={{paddingLeft: `10px`,fontSize: `50px`, color: `#ffffff`}}>More games coming soon!</h2>
          </div>
          {/* <CatBtn /> */}
          <HomeDis dis={PageList[0].dis} h1={PageList[0].h1}/>
          {/* <FooterDis /> */}
          <CookieCom />
            </div>
          </div>
          
        </div>
      </div>
      <FooterCom />
      <Snow_fx />
    </>
  );
}